/*------ 22. contact Page  ------*/
.contact-area-new {
  background: #f7f7f7;
}
.custom-new h4 {
  font-size: 27px;
  font-weight: 600;
  padding: 15px 0px 11px 0px;
}
.contact-map {
  position: relative;

  height: 560px;
  @media #{$xs-layout} {
    height: 400px;
  }
}

.contact-info-wrap {
  padding: 65px 45px 59px 45px;

  background-color: #f3f3f3;
  @media #{$lg-layout} {
    padding: 120px 20px 116px 40px;
  }
  @media #{$md-layout} {
    padding: 120px 20px 116px 30px;
  }
  @media #{$xs-layout} {
    margin-top: 20px;
    margin-bottom: 30px;
    padding: 50px 20px 45px 30px;
  }

  .contact-icon-new {

    // @media #{$md-layout} {
    //   bottom: 10px !important;
    // }
    // @media #{$lg-layout} {
    //   bottom: 10px !important;
    // }
   
    
    // @media #{$lg-layout} {
    //   bottom: 22px !important;
    // }
    // @media #{$xs-layout} {
    //   bottom: 12px;
    // }
  }


  .contact-icon-new {
    margin-right: 20px;
    position: relative !important;
    bottom: 25px;
    i {
      font-size: 20px;
      line-height: 40px;

      display: inline-block;

      width: 40px;
      height: 40px;

      transition: all 0.3s ease 0s;
      text-align: center;

      color: #252525;
      border: 1px solid #252525;
      border-radius: 100%;
    }
    &:hover .contact-icon-new i {
      color: #fff;
      border: 1px solid #c1c1c1;
      background-color: #00b7c1;
    }
  }

  .single-contact-info {
    display: flex;
    align-items: center;

    margin-bottom: 40px;
    .contact-icon {
      margin-right: 20px;
      i {
        font-size: 20px;
        line-height: 40px;

        display: inline-block;

        width: 40px;
        height: 40px;

        transition: all 0.3s ease 0s;
        text-align: center;

        color: #252525;
        border: 1px solid #252525;
        border-radius: 100%;
      }
    }
    .contact-info-dec {
      p {
        line-height: 1;

        margin: 0 0 9px;

        color: #404040;
        a {
          color: #404040;
          &:hover {
            color: $theme-color;
          }
        }
        &:last-child {
          margin: 0;
        }
      }
    }
    &:hover .contact-icon i {
      color: #fff;
      border: 1px solid #c1c1c1;
      background-color: #00b7c1;
    }
  }
}





.contact-social {
  margin-top: 58px;
  h3 {
    font-size: 24px;
    font-weight: 500;
    line-height: 1;

    margin: 0 0 17px;

    color: #4d4d4d;
  }
  ul {
    li {
      display: inline-block;

      margin: 0 10px;
      a {
        font-size: 16px;

        color: #4d4d4d;
        &:hover {
          color: $theme-color;
        }
      }
    }
  }
}

.contact-form {
  padding: 49px 60px 50px 60px;

  background-color: #f3f3f3;
  @media #{$lg-layout} {
    padding: 50px 50px 50px 50px;
  }
  @media #{$md-layout} {
    padding: 50px 30px 50px 30px;
  }
  @media #{$xs-layout} {
    padding: 50px 30px 50px 30px;
  }
  .contact-title {
    h2 {
      font-size: 24px;
      font-weight: 500;
      line-height: 1;

      margin-bottom: 36px;

      color: #464646;
    }
  }
  .contact-form-style {
    input,
    textarea {
      height: 47px;
      margin-bottom: 23px;
      padding: 2px 14px;
      border-radius: 6px;
      color: #000;
      border: 1px solid #c1c1c1;
      background: transparent;
    }
    textarea {
      height: 138px;
      margin-bottom: 0;
      padding: 20px 14px;
    }
    button {
      font-size: 14px;
      font-weight: 500;
      line-height: 1;

      margin-top: 15px;
      padding: 15px 52px;

      text-transform: uppercase;

      color: #fff;
      border: none;
      background-color: #00b7c1;
      &:hover {
        background-color: $theme-color;
      }
    }
  }
  p {
    color: #333;
    &.success {
      margin-top: 10px;
    }
  }
}

.contact-form-style {
  .row {
    & div[class^="col-"] {
      padding-right: 15px;
      padding-left: 15px;
    }
  }
}
.contact-area {
  @media #{$xs-layout} {
    &.pb-100 {
      padding-bottom: 60px;
    }
  }
}
