/*------ 24. login register Page  --------*/
.login-form-container h3 {
  text-align: center;
  font-size: 27px;
  font-weight: 600;
  margin-top: -20px;
  color: #00b7c1;
  margin-bottom: 24px;
}
.button-box-new {
  margin-top: 10px;
}
@media only screen and (max-width: 425px){
  .button-box-new {
    margin-top: 0px !important;
}
.login-register-wrapper .login-register-tab-list {
  margin-bottom: 25px !important;
}
.login-form-container h3 {
  font-size: 22px !important;
  margin-bottom: 14px !important;
}
.login-register-wrapper .login-register-tab-list .nav-item a h4 {
  font-size: 19px !important;

}}

@media only screen and (max-width: 320px){

.login-register-wrapper .login-form-container .login-register-form form .button-box .login-toggle-btn label {
    font-size: 12px !important;
}
.login-register-wrapper .login-form-container .login-register-form form .button-box .login-toggle-btn a {
  font-size: 12px !important;
}



}

textarea {
  font-size: 14px;
  width: 100%;
  color: #686161;
  padding: 10px;
  border: 1px solid #ebebeb;
  resize: vertical;
  /* border: 1px solid #eceff8; */
  background: transparent;
}
label.new-ml-10 {
  padding-left: 10px;
  color: #726565;
}
h6.new-title {
  padding: 10px 0px 4px;
  color: #4e4747;
  font-size: 15px;
}
.login-register-wrapper {
  .login-register-tab-list {
    display: flex;
    justify-content: center;
    margin-bottom: 40px;
    .nav-item {
      position: relative;
      a {
        background: none;
        padding: 0;

        h4 {
          font-size: 25px;
          font-weight: 700;
          margin: 0 20px;
          text-transform: capitalize;
          transition: all 0.3s ease 0s;
        }
        &:last-child::before {
          display: none;
        }
      }
      a.active h4 {
        color: $theme-color;
      }

      &:before {
        background-color: #454545;
        bottom: 5px;
        content: "";
        height: 18px;
        margin: 0 auto;
        position: absolute;
        right: -2px;
        transition: all 0.4s ease 0s;
        width: 1px;
      }

      &:last-child {
        &::before {
          display: none;
        }
      }
    }
  }
  .login-form-container {
    background: transparent none repeat scroll 0 0;
    box-shadow: 0 0 6px rgba(0, 0, 0, 0.1);
    padding: 42px;
    text-align: left;
    @media #{$lg-layout} {
      padding: 80px 50px;
    }
    @media #{$xs-layout} {
      padding: 40px 15px;
    }
    .login-register-form {
      form {
        input {
          background-color: transparent;
          border: 1px solid #ebebeb;
          color: #333;
          font-size: 14px;
          height: 45px;
          margin-bottom: 15px;
          padding: 0 15px;
        }
        input[type="checkbox"] {
          height: 15px;
          margin: 0;
          position: relative;
          top: 1px;
          width: 17px;
        }
        .button-box {
          .login-toggle-btn {
            padding: 10px 0 19px;
            label {
              color: #242424;
              font-size: 15px;
              font-weight: 400;
              @media #{$xs-layout} {
                font-size: 14px;
              }
            }
            a {
              color: #242424;
              float: right;
              font-size: 15px;
              @media #{$xs-layout} {
                font-size: 14px;
              }
              transition: all 0.3s ease 0s;
              &:hover {
                color: $theme-color;
              }
            }
          }
          button {
            background-color: #00b7c1;
            border: medium none;
            color: #fff;
            cursor: pointer;
            font-size: 14px;
            font-weight: 500;
            line-height: 1;
            padding: 14px 30px;
            width: 100%;
            text-transform: uppercase;
            transition: all 0.3s ease 0s;
            &:hover {
              background-color: $theme-color;
              color: #fff;
            }
          }
        }
      }
    }
  }
}

button.button-box-new {
  background-color: #00b7c1;
  border: medium none;
  color: #fff;
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
  line-height: 1;
  padding: 13px 30px;
  text-transform: uppercase;
  transition: all 0.3s ease 0s;
  display: flex;
  text-align: center;
  justify-content: center;
  width: 100%;
  margin: auto;
}

.login-register-area {
  @media #{$xs-layout} {
    &.pb-100 {
      padding-bottom: 60px;
    }
  }
}
