.single-banner {
  position: relative;

  overflow: hidden;
  a {
    img {
      width: 100%;

      transition: all 0.5s ease 0s;
      transform: scale(1);
    }
  }
  &:hover img {
    transform: scale(1.2);
  }
  .banner-content {
    position: absolute;
    top: 40px;
    left: 36px;

    content: "";
    @media #{$lg-layout} {
      top: 20px;
      left: 20px;
    }
    @media #{$md-layout} {
      top: 10px;
      left: 15px;
    }
    @media #{$xs-layout} {
      top: 50%;
      left: 15px;

      transform: translateY(-50%);
    }
    h3 {
      font-family: "Cormorant Garamond";
      font-size: 36px;
      font-weight: 600;

      margin: 0;

      color: #915342;
      @media #{$md-layout} {
        font-size: 27px;
      }
      @media #{$xs-layout} {
        font-size: 30px;
      }
    }
    h4 {
      font-family: "Cormorant Garamond";
      font-size: 18px;
      font-weight: 600;

      margin: 3px 0 58px;

      color: #010101;
      @media #{$lg-layout} {
        margin: 3px 0 25px;
      }
      @media #{$lg-layout} {
        margin: 3px 0 15px;
      }
      @media #{$md-layout} {
        margin: 3px 0 8px;
      }
      @media #{$xs-layout} {
        margin: 3px 0 20px;
      }
      span {
        font-size: 20px;
        line-height: 1;

        position: relative;
        top: 2px;

        display: inline-block;

        margin: 0 0 0 2px;

        color: #935644;
      }
    }
    a {
      font-size: 14px;

      display: inline-block;

      width: 28px;
      height: 28px;

      text-align: center;

      color: #97584a;
      border: 2px solid #97584a;
      border-radius: 100%;
      i {
        line-height: 25px;
      }
      &:hover {
        color: $theme-color;
        border: 2px solid $theme-color;
      }
    }
    &.banner-pink {
      a {
        color: #ed59a0;
        border: 2px solid #ed59a0;
        &:hover {
          color: $theme-color;
          border: 2px solid $theme-color;
        }
      }
    }

    &--style2 {
      h3 {
        color: #1c1c1c;
      }
      a {
        color: #c61a32;
        border-color: #c61a32;
      }
    }
  }
  &.banner-green-color {
    .banner-content {
      h3 {
        color: #617d05;
      }
      h4 {
        color: #010101;
        span {
          color: #617d05;
        }
      }
      a {
        color: #617d05;
        border: 2px solid #617d05;
        &:hover {
          color: #aabb72;
          border: 2px solid #aabb72;
        }
      }
    }
  }
  &.banner-red-color {
    .banner-content {
      h3 {
        color: #df262b;
      }
      h4 {
        color: #010101;
        span {
          color: #df262b;
        }
      }
      a {
        color: #df262b;
        border: 2px solid #df262b;
        &:hover {
          color: #dc6266;
          border: 2px solid #dc6266;
        }
      }
    }
  }
  &.banner-shape {
    border: 4px solid #fff;
  }
  .banner-position-hm15-1 {
    position: absolute;
    top: 45px;
    right: 40px;
    @media #{$xl-layout} {
      top: 25px;
    }
    @media #{$lg-layout} {
      top: 20px;
      right: 20px;
    }
    @media #{$md-layout} {
      top: 20px;
      right: 20px;
    }
    @media #{$xs-layout} {
      top: 15px;
      right: 15px;
    }
    @media #{$sm-layout} {
      top: 30px;
      right: 30px;
    }
  }
  .banner-position-hm15-2 {
    position: absolute;
    top: 45px;
    left: 40px;
    @media #{$xl-layout} {
      top: 25px;
    }
    @media #{$lg-layout} {
      top: 20px;
      left: 20px;
    }
    @media #{$md-layout} {
      top: 20px;
      left: 20px;
    }
    @media #{$xs-layout} {
      top: 15px;
      left: 15px;
    }
    @media #{$sm-layout} {
      top: 30px;
      left: 30px;
    }
  }
  .banner-position-hm17-1 {
    position: absolute;
    right: 105px;
    bottom: 50px;
    @media #{$md-layout} {
      right: 20px;
      bottom: 20px;
    }
    @media #{$xs-layout} {
      right: 20px;
      bottom: 20px;
    }
  }
  .banner-content-3 {
    h3 {
      font-size: 30px;
      font-weight: bold;
      line-height: 1;

      margin: 0;

      text-transform: uppercase;

      color: #111111;
      @media #{$xl-layout} {
        font-size: 25px;
      }
      @media #{$lg-layout} {
        font-size: 25px;
      }
      @media #{$md-layout} {
        font-size: 22px;
      }
      @media #{$xs-layout} {
        font-size: 20px;
      }
    }
    p {
      font-size: 18px;

      margin: 11px 0 20px;

      color: #010101;
      @media #{$xl-layout} {
        margin: 5px 0 12px;
      }
      @media #{$lg-layout} {
        margin: 5px 0 12px;
      }
      @media #{$md-layout} {
        margin: 5px 0 10px;
      }
      @media #{$xs-layout} {
        margin: 5px 0 10px;
      }
      span {
        color: #6eab49;
      }
    }
    > a {
      font-size: 14px;

      display: inline-block;

      width: 28px;
      height: 28px;

      text-align: center;

      color: #6eab49;
      border: 2px solid #6eab49;
      border-radius: 100%;
      &:hover {
        color: $theme-color;
        border: 2px solid $theme-color;
      }
    }
    &.pink-banner {
      p {
        color: #010101;
        span {
          color: #ed59a0;
        }
      }
      > a {
        color: #ed59a0;
        border: 2px solid #ed59a0;
        &:hover {
          color: $theme-color;
          border: 2px solid $theme-color;
        }
      }
    }
  }
  .banner-content-4 {
    span {
      font-size: 18px;

      color: #6eab49;
    }
    h2 {
      font-size: 55px;
      font-weight: bold;
      line-height: 1;

      margin: 15px 0 15px;

      text-transform: uppercase;

      color: #f3993f;
      @media #{$xl-layout} {
        font-size: 40px;

